	module.exports = {"data":{"markdownRemark":{"html":"<p><strong>お仕事の話とか</strong> </p>\n<p>ウチの作った体験版Flash(詳細は先日の日記参照)が公式サイトで公開されたようです。まぁ強いて言うならロード画面作りたかったんですが……。(8MBくらいあるんで) しかし<a href=\"http://www.adobe.com/jp/products/flex/\">Flex</a>面白いなぁ。今回の体験版Flashで初めてFlexを触ったが、すっかりはまりこんでしまった。FlashでActionScriptバリバリ使ったFlashアプリ作れって言われたらかなり身構えてしまいますが、Flexだとメインループから自力で作りこんでいく形なので、それがいとも簡単に出来てしまう。言ってしまえば、Javaアプレットでも作るかのように。IDEもEclipseベースで馴染み易いし。(DSアプリ作るときに使ったCodeWarriorは慣れるのに恐ろしく時間がかかった)ActionScript3.0の言語仕様に慣れるのに若干時間かかったが、それでもFlashLite1.1やPHP、Objective-Cよりは楽だったなぁ。(PHPは小さいのを作る分には恐ろしく便利だが、デカブツのを作る気にはチトチトなれない。Objective-Cは……聞くな)と思ったら今度は<a href=\"http://slashdot.jp/developers/article.pl?sid=08/11/23/0717231\">ActionScriptの代わりにC/++でFlashアプリが作れる</a>とか。これはｗｋｔｋせざるを得ない。</p>\n<h2>冬コミの出し物</h2>\n<p><a href=\"http://dangoya.moo.jp/\">DANGOYA</a>さんと共同制作の作品“thM(仮称)”の体験版を出そうかと思ってましたが、諸々の事情で延期となりましたので、<del><a href=\"/!/thA/\">時封城</a>リメイクの体験版を配布します。(いや完全実装はちょっと間に合いそうにないもので orz)時封城(400円)を購入していただいた方に無料で付録いたします。また、既に時封城を持っている方は時封城のジャケットを持参していただければ無料で差し上げます。</del>あとは既刊として時封城(CD-R版)、夢終劇(部数が少ないのでお早めに)、レイラ、danmaq soundtrackとなります。</p>\n<h2>更新情報</h2>\n<p><a href=\"/tag/1123\">SS1個公開</a>。VOCALOIDもの(マスター×ミク)、非エロ/シリアスものです。</p>","frontmatter":{"cover":null,"date":"2008-11-25T00:00:00+09:00","strDate":"2008/11/24","title":"The・会社ヒッキー"}}},"pathContext":{"path":"/ja/blog/2008/11/25-the・会社ヒッキー/","langKey":"ja"}}

